.flex {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.div {
  align-items: flex-start;
  background: #152737;
  border-radius: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-width: 304px;
  padding: 16px 16px 12px;
}

.title {
  color: #fff;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  margin: 0;
}

.form {
  display: flex;
  flex-direction: column;
  margin-top: 16px;
  width: 100%;
}

.btn {
  margin-top: 10px;
}

.mt0 {
  margin-top: 0;
}

.link {
  color: #b3c0cc;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  margin-top: 22px;
  text-align: center;
  text-decoration: none;
  transition: 0.2s;
  text-align: center;
  display: block;
  width: 100%;
}

.err {
  border: 2px solid red;
}

.errMsg {
  margin: 8px 0 8px 0;
  font-size: 12px;
  color: red;
  text-align: center;
}
