.menu {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 20px;
  top: 140px;
  box-sizing: border-box;
  z-index: 2;
}

.burger {
  display: none;
  margin-left: 20px;
  margin-top: 20px;
  width: 60px;
  height: 40px;
  transition: 0.2s;
  cursor: pointer;
}

.burger:hover {
  opacity: 0.7;
}

.span {
  display: none;
  width: 100%;
  height: 5px;
  background: #fff;
}

.link {
  color: #fff;
  text-decoration: none;
  padding: 10px;
  transition: 0.2s;
}

.links {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  position: fixed;
  top: 0;
  left: -200%;
  width: 100vw;
  transition: 0.2s;
  align-items: center;
  background: #152737;
  z-index: 200;
}

.btn {
  background: transparent;
  font-size: 60px;
  color: #fff;
  border: 0;
  position: absolute;
  top: 20px;
  right: 20px;
  transition: 0.2s;
  cursor: pointer;
}

.btn:hover {
  opacity: 0.7;
}

.linksActive {
  left: 0;
}

.linkM {
  color: #fff;
  text-decoration: none;
  padding: 20px;
  margin-top: 10px;
  cursor: pointer;
  transition: 0.2s;
}

.linkM:hover {
  background: #0a1d2a;
}

.linkM:first-of-type {
  margin-top: 0;
}

.link:hover {
  background: #152737;
}

.link:first-child {
  margin-top: 0;
}

@media screen and (max-width: 1250px) {
  .menu {
    display: none;
  }
  .burger {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .span {
    display: block;
  }
}
