.block {
  max-width: 880px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 40px auto 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.title {
  color: #fff;
  margin: 0;
}

.form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.input {
  background: #0a1d2a;
}

.btn {
  margin-top: 20px;
  max-width: 240px;
  align-self: center;
}

.ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.result {
  color: #fff;
  margin: 20px 0 0 0;
}

.li {
  margin-top: 16px;
}

.err {
  margin: 0;
  margin-top: 8px;
  font-size: 12px;
  color: red;
  text-align: center;
}
